body{
  height:100vh;
  margin:0;
  font-family: Raleway, "Helvetica Neue", "Lucida Grande", Arial, Verdana, sans-serif;
  box-sizing: border-box;

}
.slides{
  height:100%;
  width:100%;
  position:relative;
  overflow: hidden;
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}
/*Homeslide style*/
.inactive-homeslide{
  display:none;
  transition: opacity 0.3s ease-in-out;
  -webkit-transition:opacity 0.3s ease-in-out;
  background : linear-gradient(0deg, rgba(0,0,0,0.6), transparent);
}
.homeslide-text-container{
  display:block;
  margin:auto;
  text-align: center;
  transition: opacity 0.3s ease-in-out;
  -webkit-transition:opacity 0.3s ease-in-out;
  .homeslide-header{
    h1{
      color:#ffffff;
      text-shadow: 2px 2px 4px #000000;
      font-size: 5em;
    }
  } 
  p{
    color:#cecece;
    text-shadow: 1px 1px 2px #000000;
    font-size: larger;
    max-width: 50%;
    margin:auto
  }
}
.homeslide-button-start{
  border: 1px solid #e0001a;
  background: #e0001a;
  color: #ffffff;
}
.active-homeslide{
  height:100%;
  width:100%;
  background : linear-gradient(0deg, rgba(0,0,0,0.6), transparent);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display:flex;
  transition: opacity 0.3s ease-in-out;
  -webkit-transition:opacity 0.3s ease-in-out;
  .about-closed{
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    transition: all 0.4s ease;
    position: fixed;
    z-index: 70;
    top: 0;
    right:-50%;
    width: 50%;
    height: 100%;
  }
  
  }
  .about-open{
    pointer-events: all;
    position: fixed;
    z-index: 70;
    top: 0;
    width: calc(50% - 8rem);
    right:0;
    height: 100%;
    background: rgba(255,255,255,0.9);
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    transition: all 0.4s ease;
    padding: 2.5rem 4rem 5rem;
    overflow-y:auto;
    .close-content{
      font-size: 2em;
      line-height: 0;
      position: fixed;
      top: .5rem;
      right: 1.5rem;
      width: 2rem;
      height: 2rem;
      opacity: 1;
      color: #999999;
      border: none;
      background: url('https://api.iconify.design/ion-close-round.svg') no-repeat center center / contain;
      cursor: pointer;
      transition: all 0.2s ease-in-out;
      margin: 0;
      font: inherit;
      text-transform: none;
    }
    h2{
      font-weight: 400;
      line-height: 1;
      margin: 0 0 1.5rem 0;
      margin-bottom: 1.2rem;
      text-align: left;
      color: #22264b;
      font-size: 2.3rem;
    }
    .about-image{
      margin-left: auto;
      margin-right: auto;
      margin-top: 2rem;
      margin-bottom: 2rem;
      img{
        position: relative;
        max-width: 100%;
        max-height: 30%;
      }
    }
    small{
      font-family: Lora, "Helvetica Neue", "Lucida Grande", Arial, Verdana, sans-serif;
      font-size: 1rem;
      font-style: italic;
      display: block;
      margin-top: 0rem;
      color: rgba(0, 0, 0, 0.5);
      font-weight: 400;
      line-height: 0.5;
      margin: 0;
      padding: 0;
      vertical-align: baseline;
      border: 0;
    }
    p{
      padding: 0;
      vertical-align: baseline;
      border: 0;
      font-family: "Open Sans", "Helvetica Neue", "Lucida Grande", Arial, Verdana, sans-serif;
      font-size: 1rem;
      font-weight: 400;
      margin: 0;
      color: #666666;
    }
    .partners{
      
      .seperator{
        span{
          display: block;
          width: 100%;
          height: 2px;
          margin: 50px 0 60px;
          background: #f2f3f7;
        }
      }
      a{
        position: relative;
        display: inline-block;
        opacity: .3;
        width: 25%; /* xs = 50%*/
        cursor: pointer;
        text-decoration: none;
        color: #FFFFFF;
        -webkit-transition: all 0.2s ease-in-out;
        -moz-transition: all 0.2s ease-in-out;
        transition: all 0.2s ease-in-out;
        img{
          max-height: 3rem;
          margin: 2rem auto 2rem;
          display: block;
          max-width: 100%;
          height: auto;
        }
      }:hover{
        opacity: 1;
      }
    }
  
}
.scroll-indicator {
  font-family: Raleway, "Helvetica Neue", "Lucida Grande", Arial, Verdana, sans-serif;
  font-size: 1.1rem;
  font-weight: 400;
  position: absolute;
  z-index: 2;
  bottom: 20px;
  left: 50%;
  display: inline-block;
  padding-top: 70px;
  letter-spacing: .1rem;
  text-transform: uppercase;
  color: #FFFFFF;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%);
  cursor: pointer;
  text-decoration: none;
  color: #FFFFFF;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  span {
    position: absolute;
    top: 0;
    left: 50%;
    box-sizing: border-box;
    width: 24px;
    height: 24px;
    margin-left: -12px;
    border-bottom: 1px solid #FFFFFF;
    border-left: 1px solid #FFFFFF;
    -webkit-transform: rotate(
  -45deg
  );
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(
  -45deg
  );
    -webkit-animation: scrolling 1.5s infinite;
    -moz-animation: scrolling 1.5s infinite;
    animation: scrolling 1.5s infinite;
  }
}
.video-foreground, .video-background video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.video-background {
  background: #000;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.video-foreground, .video-background video, .video-background iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border:none;
}
.customer-logo{
position: absolute;
bottom: 16px;
right: 16px;
z-index: 6;
max-width: 20%;
max-height: 10%;
}
.homeslide-navbar{
  position: fixed;
  z-index: 45;
  top: 0;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  white-space: nowrap;
  opacity: 1;
  height :2.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  transition: all 0.4s ease;
  li {
    font-family: Lato, "Helvetica Neue", "Lucida Grande", Arial, Verdana, sans-serif;
    line-height: 2.5rem;
    position: relative;
    display: inline-block;
    float: left;
    min-width:5rem;
    height :2.5rem;
    margin: 0;
    text-align: center;
    color: #FFFFFF;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    transition: all 0.4s ease;
    :hover {
      span {
      visibility: visible;
      opacity: 1;
      color: #FFFFFF;
      -webkit-transform: translateY(0) rotateX(0);
      -moz-transform: translateY(0) rotateX(0);
      -ms-transform: translateY(0) rotateX(0);
      -o-transform: translateY(0) rotateX(0);
      transform: translateY(0) rotateX(0);
    }
    ::before {
      opacity: 0;
    }
    ::after {
      opacity: 1;
    }
  }
  a {
    font-weight: 400;
    display: block;
    overflow: visible;
    padding: 0 2rem;
    text-decoration: none;
    text-transform: uppercase;
    color: #FFFFFF;
    cursor: pointer;
    span {
      font-family: Raleway, "Helvetica Neue", "Lucida Grande", Arial, Verdana, sans-serif;
      font-size: 1.1rem;
      font-weight: 700;
      position: absolute;
      bottom: -32px;
      left: .5rem;
      visibility: hidden;
      text-align: left;
      letter-spacing: .1rem;
      opacity: 0;
      color: #FFFFFF;
      -webkit-transition: all 0.4s ease;
      -moz-transition: all 0.4s ease;
      transition: all 0.4s ease;
      -webkit-transform: translateY(2rem) rotateX(90deg);
      -moz-transform: translateY(2rem) rotateX(90deg);
      -ms-transform: translateY(2rem) rotateX(90deg);
      -o-transform: translateY(2rem) rotateX(90deg);
      transform: translateY(2rem) rotateX(90deg);
    }
  }
  }
  .menu-link {
    position: absolute;
    z-index: 80;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0;
    li {
      font-family: Raleway, "Helvetica Neue", "Lucida Grande", Arial, Verdana, sans-serif;
      font-size: 1.1rem;
      font-weight: 700;
      display: inline-block;
      letter-spacing: .1rem;
      text-transform: uppercase;
      a {
        font-weight: 700;
        display: block;
        padding: 0 2rem;
        i{
          font-weight: 700;
          display: block;
          padding: 8px;
          writing-mode: tb;
          font-size: larger;
        }
        span {
          writing-mode: initial;
          font-size: 1.1rem;
          position: absolute;
          right: .5rem;
          left: auto;
          visibility: hidden;
          opacity: 0;
          color: #FFFFFF;
          -webkit-transition: all 0.4s ease;
          -moz-transition: all 0.4s ease;
          transition: all 0.4s ease;
          -webkit-transform: translateY(2rem) rotateX(90deg);
          -moz-transform: translateY(2rem) rotateX(90deg);
          -ms-transform: translateY(2rem) rotateX(90deg);
          -o-transform: translateY(2rem) rotateX(90deg);
          transform: translateY(2rem) rotateX(90deg);
        }
        :hover {
          color: #FFFFFF;
          background: rgba(255, 255, 255, 0.2);
          span {
            visibility: visible;
            opacity: 1;
            color: #FFFFFF;
            -webkit-transform: translateY(0) rotateX(0);
            -moz-transform: translateY(0) rotateX(0);
            -ms-transform: translateY(0) rotateX(0);
            -o-transform: translateY(0) rotateX(0);
            transform: translateY(0) rotateX(0);
          }
        }
      }
    }
  }
}
/* slide style*/
.slide{
  position: relative;
  height:100%;
  width:100%;
  transition: opacity 0.5s ease-in-out;
  -webkit-transition:opacity 0.5s ease-in-out;
  background : linear-gradient(15deg, rgba(0,0,0,0.6), transparent);

}
.inactive-slide{
  display:none;
  transition: opacity 0.3s ease-in-out;
  -webkit-transition:opacity 0.3s ease-in-out;
}
.slide-start-button{
  border: 1px solid #e0001a;
  background: #e0001a;
  color: #ffffff;
}
.active-slide{
  height:100%;
  width:100%;
  display:flex;
  transition: opacity 0.3s ease-in-out;
  -webkit-transition:opacity 0.3s ease-in-out;
}

.slide-icon-paragraph {
  i{
    padding-right:8px;
  }
}
.slide-fotograf{
  font-weight: 700;
  display:inline;
  ::before{
    content: "Foto von ";
    font-weight:normal;
  }
}
.slide-location{
  display:inline-block;
}
.slide-button-container{
  position:fixed;
  display:flex;
  flex-direction: row;
  align-self: flex-end;
  margin:8px;
}
.slide-button{
  display: inline-block;
  font-family: Raleway, "Helvetica Neue", "Lucida Grande", Arial, Verdana, sans-serif;
  font-weight: 700;
  margin-right: 1rem;
  padding: .7em 2em;
  cursor:pointer;
  z-index: 5;
  margin:4px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  border-radius: 3px;
  transition: all 0.25s ease-in-out;
  :hover{
    color: #22264b;
    border-color: #FFFFFF;
    background: #FFFFFF;
}
}
.slide-button-facebook{
  color: #FFFFFF;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background: rgba(45, 49, 56, 0.1);
}
.nav-container{
  display:flex;
  position: fixed;
  height:100%;
  width:100%;
  z-index: 5;
  pointer-events: none;
  .nav-left{
    display:block;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 16px;
    cursor:pointer;
    pointer-events: all;
    transition: transform 0.2s ease-in-out;
    img{
      transform : rotate(180deg);
      height: 64px;
      padding:8px;
      padding-right: 4px;
    }
  }
  .nav-right{
    display:block;
    margin-top: auto;
    margin-bottom: auto;
    margin-left:auto;
    margin-right: 16px;
    cursor:pointer;
    pointer-events: all;
    transition: transform 0.2s ease-in-out;
    :hover{
      transform : scale(1.1,1.1);
    }
    img{
      height: 64px;
      padding:8px;
      padding-right: 4px;
    }
  }
}

.slide-text-container{
