      background: #888;
      border-radius: 50%;
      display: inline-block;
      width:20px;
      height:20px;
      cursor: pointer;
      margin-left:8px;
      pointer-events: all;
    }
    .active-dot{
      background: rgb(197, 197, 197);
      width:22px;
      height:22px;
    }
