/*sm*/
@media (max-width: 768px) { 
  .slide-text-container{
  max-width: calc(100% - 62px);
  }
}
/*xs*/
@media (max-width: 576px) {
  .slide-pagination-container{
    ol{
      margin-bottom: 72px;
    }
  } 
  .subslide-pagination-container{
    margin-bottom: 40px;
  }
}


